 import React from "react"

class AppTwo extends React.Component{
    constructor(){
        super()
        this.state = {
            IsLoggedIn : false
        }
    }
    render(){
        return(
            <div>
                <h1> You are currently Logged {this.state.IsLoggedIn ? "In" : "Out" }</h1>
            </div>
        )
    }

}
export default AppTwo